  // å¦‚æžœè¦è€ƒè™‘é€šç”¨æ€§ï¼Œè¿™é‡Œåº”è¯¥ç”¨ä¸‰è§’å‡½æ•° Math.sin(45)
  // ä½†æ˜¯è¿™ä¸ªåœºæ™¯æ¯”è¾ƒç‰¹æ®Šï¼Œå§‹ç»ˆæ˜¯ç­‰è…°ç›´è§’ä¸‰è§’å½¢ï¼Œæ‰€ä»¥ç›´æŽ¥ç”¨ Math.sqrt() å¼€æ–¹å³å¯
  return radius - Math.sqrt(Math.pow(radius, 2) / 2);
